const Home = ( { data } ) => {

  const slides = slidesData.map((slide, index) => ({
    ...slide,
    image: data["image" + (index + 1)].childImageSharp.fluid

  }))

  return (
    <Layout activePage="">
      <PresentationBar images={data} slides={slides}/>
      
      <div className="section-cards">
          No elo!
      </div>
    </Layout>
  )
}

const PresentationBar = ( props ) => {

  const [ currentSlide, setCurrentSlide ] = useState(0)
  const [ shifting, setShifting ] = useState(true)
  
  useEffect(() => {
    const changeSlides = () => {
      setCurrentSlide( prevVal => (parseInt(prevVal) + 1) % props.slides.length)
    }

    
    const intervals = setInterval(changeSlides, 3000);

    if (shifting === false) {
      clearInterval(intervals)
    } 
    
    return () => {
      clearInterval(intervals)
    }

  },[shifting, props.slides.length])

  const changeSlide = (val) => {
    console.log("slide changed to: " + val)
    setCurrentSlide(val)
    setShifting(false)
    setTimeout(() => setShifting(true), 60000)
  } 

  // const slides = [
  //   {
  //     id: 1,
  //     image: props.images.image1.childImageSharp.fluid,
  //     title: "Pa jakie mam łóżko",
  //     comment: "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Curabitur interdum in ipsum non pretium. Curabitur egestas, libero eu volutpat volutpat, nisi ante condimentum mi, et viverra ex turpis eget elit. Duis non mauris vel enim cursus rhoncus eu id ex. Praesent a est id tellus consectetur commodo. Quisque eu dui a ante sollicitudin facilisis sit amet nec tellus. Orci varius natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus."
  //   },
  //   {
  //     id: 2,
  //     image: props.images.image2.childImageSharp.fluid,
  //     title: "No elo, jestem Wera.",
  //     comment: "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Curabitur interdum in ipsum non pretium. Curabitur egestas, libero eu volutpat volutpat, nisi ante condimentum mi, et viverra ex turpis eget elit. Duis non mauris vel enim cursus rhoncus eu id ex. Praesent a est id tellus consectetur commodo. Quisque eu dui a ante sollicitudin facilisis sit amet nec tellus. Orci varius natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus."
  //   },
  //   {
  //     id: 3,
  //     image: props.images.image3.childImageSharp.fluid,
  //     title: "Mmm... Jak pachnąco.",
  //     comment: "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Curabitur interdum in ipsum non pretium. Curabitur egestas, libero eu volutpat volutpat, nisi ante condimentum mi, et viverra ex turpis eget elit. Duis non mauris vel enim cursus rhoncus eu id ex. Praesent a est id tellus consectetur commodo. Quisque eu dui a ante sollicitudin facilisis sit amet nec tellus. Orci varius natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus."
  //   }
  // ]

  


  return(
    <div className="presentation-bar">
      {//slides[currentSlide] 
      props.slides.map((slide, index) => {
        //if (currentSlide === index) {
          const isActive = currentSlide === index ? true : false

          return (
            <PresentationBarSlide
              key={"slide" +index}
              image={slide.image}
              title={slide.title}
              comment={slide.comment}
              active={isActive}
            />
          )
        //}
      }
        )
      }
      <PresentationBarNav 
        handleClick={changeSlide}
        itemCount={props.slides.length}
        activeItem={currentSlide}
      />
    </div>
  )
}

const PresentationBarSlide = (props) => {
  return (
    <div className={`presentation-bar-main ${props.active ? "active" : ""}`} >
      <Img
        className="image-bg"
        fluid={props.image}
        //fluid={props.image}
        //image: props.images.image3.childImageSharp.fluid,
      />
      <p className={"title"}>{props.title}</p>
      <p className={"comment"}>{props.comment}</p>
    </div>
  )
}

const PresentationBarNav = (props) => {

  const buttons = []
  
  for (let i = 0; i < props.itemCount; i++) {

    buttons.push(
      <button
        aria-label={`Select ${i} slide`}
        className={`presentation-bar-btn ${props.activeItem === i ? "presentation-bar-btn-active" : ""}`}
        onClick={() => props.handleClick(i)}
        key={"index" + i}
      ></button>
    )
  }
  

  return(
    <div className="presentation-bar-nav">
      {buttons}
      {/* <button className="presentation-bar-btn" onClick={() => props.handleClick(0)}></button>
      <button className="presentation-bar-btn" onClick={() => props.handleClick(1)}></button>
      <button className="presentation-bar-btn" onClick={() => props.handleClick(2)}></button> */}
    </div>
  )
}

export default Home

export const image = graphql`
fragment imageBig on File {
  childImageSharp {
    fluid {
      ...GatsbyImageSharpFluid
    }
  }
}
`

export const data = graphql`
query {
  image1: file(relativePath: { eq: "images/salon.jpg" }) {
    ...imageBig
  }
  image2: file(relativePath: { eq: "images/ryj.jpg" }) {
    ...imageBig
  }
  image3: file(relativePath: { eq: "images/wellness.jpg" }) {
    ...imageBig
  }
}
`